<template>
  <v-container>
    <v-toolbar flat color="white">

      <v-card-title>
        <v-text-field v-model="search" label="Search (case sensitive)" single-line hide-details></v-text-field>
      </v-card-title>

      <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
      
      <v-divider class="mx-4" inset vertical></v-divider>
      
      <!-- <v-spacer></v-spacer> -->
      
      <v-dialog v-model="dialog" max-width="500px">
      
        <template v-slot:activator="{ on }">
          <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
        </template>
      
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
                    </v-col>
                    
                    <v-col cols="12" sm="6" md="4">
                        <v-select
                            :items="drinkList"
                            v-model="editedItem.calories"
                            label="Jenis"
                        ></v-select>
                    </v-col>

                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                    </v-col>
                    
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                    </v-col>
                    
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                    </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>

    <v-row>
      <v-col cols="3" v-for="drink in filteredDrinks" :key="drink.name">
        <v-card max-width="344" outlined class="d-inline-flex">
          <v-list-item three-line>
            <v-list-item-content>
              <div class="overline mb-4">MINUMAN</div>
              <v-list-item-title class="headline mb-1">{{drink.name}}</v-list-item-title>
              <v-list-item-subtitle>Jenis: {{drink.calories}}</v-list-item-subtitle>
              <v-list-item-subtitle>Fat: {{drink.fat}}</v-list-item-subtitle>
              <v-list-item-subtitle>Carbs: {{drink.carbs}}</v-list-item-subtitle>
              <v-list-item-subtitle>Protein: {{drink.protein}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-card-actions>
            <v-icon class="mr-1" color="teal" @click="editItem(drink)">mdi-silverware</v-icon>
            <v-icon class="mr-1" color="red" @click="deleteItem(drink)">mdi-delete</v-icon>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/vue/1.0.26/vue.js"></script>

// ========================== SCRIPT ======================

<script>
import { drinksRef } from "../firebase";

export default {
  data: () => ({
    cek: -1,
    dialog: false,
    search: '',
    headers: [
      {
        text: "Drinks (100g serving)",
        align: "left",
        sortable: false,
        value: "name"
      },
      { text: "Calories", value: "calories" },
      { text: "Fat (g)", value: "fat" },
      { text: "Carbs (g)", value: "carbs" },
      { text: "Protein (g)", value: "protein" },
      { text: "Actions", value: "action", sortable: false }
    ],
    drinkList: ["Es", "Hangat"],
    drinks: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      calories: "Es",
      fat: 0,
      carbs: 0,
      protein: 0
    },
    defaultItem: {
      name: "",
      calories: "Es",
      fat: 0,
      carbs: 0,
      protein: 0
    }
  }),
  firebase: {
    drinks: drinksRef
  },
  computed: {
    formTitle() {
      return this.cek === -1 ? "New Item" : "Edit Item";
    },
    filteredDrinks: function() {
      return this.drinks.filter(drink => {
        return drink.name.match(this.search);
      });
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {},
  methods: {
    editItem(item) {
      this.editedIndex = item[".key"];
      this.cek = 0;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      confirm("Are you sure you want to delete this item?") &&
        drinksRef.child(item[".key"]).remove();
    },
    deleteItem2() {
      for (var i = 0; i < this.selected.length; i++) {
        const index = this.drinks.indexOf(this.selected[i]);
        this.drinks.splice(index, 1);
      }
      this.dialog = false;
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    clear() {
      this.editedItem = {};
      this.editedItem.name = "";
      this.editedItem.calories = "Es";
      this.editedItem.fat = 0;
      this.editedItem.carbs = 0;
      this.editedItem.protein = 0;
      this.cek = -1;
    },
    // methods untuk save
    save() {
      if (this.cek > -1) {
        drinksRef
          .child(this.editedIndex)
          .set({
            name: this.editedItem.name,
            calories: this.editedItem.calories,
            fat: this.editedItem.fat,
            carbs: this.editedItem.carbs,
            protein: this.editedItem.protein
          });
        this.clear();
      } else {
        drinksRef.push({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein
        });
        this.clear();
      }
      this.close();
    }
  }
};
</script>